/* progrss bar code start */


.container1 {
    position: relative;
    /* top:150px; */
    width: 900px;
    display: flex;
    /* justify-content: space-around; */
    /* justify-content: space-evenly; */
    
    }
    
    .container1 .card1 {
    position: relative;
    left:20px;
    width:150px;
    background: linear-gradient(0deg, #1b1b1b, #222, #1b1b1b);
    display: flex;
    justify-content: center;
    align-items: center;
    height: 200px;
    border-radius: 4px;
    text-align: center;
    overflow: hidden;
    transition: 0.5s;
    
    }
    
    .container1 .card1:hover {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    box-shadow: 0 15px 35px rgba(0, 0, 0, .5);
    }
    
    .container1 .card1:before {
    content: '';
    position: absolute;
    top: 0;
    left: -50%;
    width: 100%;
    height: 100%;
    background: rgba(255, 255, 255, .03);
    pointer-events: none;
    z-index: 1;
    }
    
    .percent1 {
    position: relative;
    width: 150px;
    height: 150px;
    border-radius: 50%;
    box-shadow: inset 0 0 50px #000;
    background: #222;
    z-index: 1000;
    }
    
    .percent1 .number1 {
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    }
    
    .percent1 .number1 h2 {
    color: #777;
    font-weight: 700;
    font-size: 40px;
    transition: 0.5s;
    position: relative;
    top:10px;
    }
    
    .card1:hover .percent1 .number1 h2 {
    color: #fff;
    font-size: 60px;
    }
    
    .percent1 .number1 h2 span {
    font-size: 24px;
    color: #777;
    transition: 0.5s;
    }
    
    .card1:hover .percent1 .number1 h2 span {
    color: #fff;
    }
    
    .text1 {
    position: relative;
    top:5px;
    color: #777;
    margin-top: 20px;
    font-weight: 700;
    font-size: 18px;
    letter-spacing: 1px;
    text-transform: uppercase;
    transition: 0.5s;
    }
    
    .card1:hover .text1 {
    color: #fff;
    }
    
    svg {
    position: relative;
    width: 150px;
    height: 150px;
    z-index: 1000;
    }
    
    svg circle
    {
    width: 100%;
    height: 100%;
    fill: none;
    stroke: #191919;
    stroke-width: 10;
    stroke-linecap: round;
    -webkit-transform: translate(5px, 5px);
            transform: translate(5px, 5px);
    }
    
    svg circle:nth-child(2) {
    stroke-dasharray: 440;
    stroke-dashoffset: 440;
    }
    
    .card1:nth-child(1) svg circle:nth-child(2) {
    stroke-dashoffset: calc(440 - (440 * 90) / 100);
    stroke: #00ffcc;
    }
    
    .card1:nth-child(2) svg circle:nth-child(2) {
    stroke-dashoffset: calc(440 - (440 * 85) / 100);
    stroke: #ff3300;
    }
    
    .card1:nth-child(3) svg circle:nth-child(2) {
    stroke-dashoffset: calc(440 - (440 * 75) / 100);
    stroke: #ffff00;
    }

    .card1:nth-child(4) svg circle:nth-child(2) {
        stroke-dashoffset: calc(440 - (440 * 82) / 100);
        stroke: #4bcffa;
        }

        .card1:nth-child(5) svg circle:nth-child(2) {
            stroke-dashoffset: calc(440 - (440 * 80) / 100);
            stroke: #0be881;
            }

            .card1:nth-child(6) svg circle:nth-child(2) {
                stroke-dashoffset: calc(440 - (440 * 78) / 100);
                stroke: #ef5777;
                }

                .card1:nth-child(7) svg circle:nth-child(2) {
                    stroke-dashoffset: calc(440 - (440 * 85) / 100);
                    stroke: yellowgreen;
                    }

                    .card1:nth-child(8) svg circle:nth-child(2) {
                        stroke-dashoffset: calc(440 - (440 * 85) / 100);
                        stroke: rgb(50, 205, 205);
                        }

                        .card1:nth-child(9) svg circle:nth-child(2) {
                            stroke-dashoffset: calc(440 - (440 * 82) / 100);
                            stroke: rgb(205, 50, 166);
                            }

                            .card1:nth-child(10) svg circle:nth-child(2) {
                                stroke-dashoffset: calc(440 - (440 * 93) / 100);
                                stroke: rgb(50, 78, 205);
                                }
    

                                .card1:nth-child(11) svg circle:nth-child(2) {
                                    stroke-dashoffset: calc(440 - (440 * 87) / 100);
                                    stroke: rgb(50, 78, 205);
                                    }

                                    .card1:nth-child(12) svg circle:nth-child(2) {
                                        stroke-dashoffset: calc(440 - (440 * 97) / 100);
                                        stroke: rgb(50, 78, 205);
                                        }

                                        .card1:nth-child(13) svg circle:nth-child(2) {
                                            stroke-dashoffset: calc(440 - (440 * 78) / 100);
                                            stroke: rgb(50, 78, 205);
                                            }

                                            .card1:nth-child(14) svg circle:nth-child(2) {
                                                stroke-dashoffset: calc(440 - (440 * 84) / 100);
                                                stroke: rgb(50, 78, 205);
                                                }

                                                .card1:nth-child(15) svg circle:nth-child(2) {
                                                    stroke-dashoffset: calc(440 - (440 * 70) / 100);
                                                    stroke: rgb(50, 78, 205);
                                                    }
